import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import elementPlus from './element/index.js'
import './style/reset.css'
import 'element-plus/dist/index.css'
import directiveApi from './directive/index.js'
import { createPinia } from 'pinia'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'

const rootApp = createApp(App)
elementPlus(rootApp) // 按需引入ui
directiveApi(rootApp) // 自定义指令

for (let [key, component] of Object.entries(ElementPlusIconsVue)) {
  rootApp.component(key, component)
}


const setFontSize= () => {
  const htmlDom = document.getElementsByTagName('html')[0];
  let htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
  if (htmlWidth >= 750) {
    htmlWidth = 750;
  }
  if (htmlWidth <= 320) {
    htmlWidth = 320;
  }
  htmlDom.style.fontSize = `${htmlWidth / 7.5}px`
};
window.onresize = setFontSize;
setFontSize()

rootApp.use(createPinia()).use(store).use(router).mount("#app");